export default {
    name: "addresslist",
    data(){
        return{
            adr:[]
        }
    },
    mounted:function () {
        let token=JSON.parse(localStorage.getItem('user')).token;
        this.$axios.get('/user/addresses',
            { headers: {
                    'Authorization': 'Bearer ' + token,
                }}).then(res=>{
            this.adr=res.data.data;
            console.log(res.data.data)
        })
    },
    methods:{
        add:function () {
            this.$router.push({name:'shouhuoadd'})
        }
    }
}



